.signupScreen {
    max-width: 300px;
    padding: 70px;
    margin: 0 auto;
    background: rgba(0, 0, 0, 0.85);
  }
  
  .signupScreen > form {
    display: grid;
    flex-direction: column;
  }
  
  .signupScreen > form > h1 {
    text-align: left;
    margin-bottom: 25px;
  }
  
  .signupScreen > form > input {
    outline-width: 0;
    height: 40px;
    margin-bottom: 14px;
    border-radius: 5px;
    border: none;
    padding: 5px 15px;
  }
  
  .signupScreen > form > button {
    padding: 16px 20px;
    font-size: 1rem;
    color: #fff;
    font-weight: 600;
    border-radius: 5px;
    background-color: #e50914;
    outline: none;
    cursor: pointer;
    margin-top: 20px;
  }
  
  .signupScreen > form > h4 {
    text-align: left;
    margin-top: 25px;
  }
  
  .signupScreen__gray {
    color: gray;
  }
  
  .signupScreen__link:hover {
    cursor: pointer;
    text-decoration: underline;
  }
  
  @media (max-width: 450px) {
    .signupScreen {
      width: 90%;
      padding: 2rem;
      margin: 0 auto;
      background: rgba(0, 0, 0, 0.85);
      height: 100%;
    }
    .signupScreen > form {
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      align-items: center;
    }
    .signupScreen > form > button {
      width: 80%;
    }
  }